@import './reset.css';

header,
footer,
section {
    align-items: center;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    width: 100%;
}

footer {
    background: black;
}

header,
footer {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    padding: 25px;

    & .logo-container {
        & svg {
            height: 25px;
            width: auto;
        }
    }

    & .menu {
        align-items: center;
        color: var(--text-color-grey);
        display: flex;
        font-size: 14px;
        justify-content: space-between;

        & .item {
            border-left: solid 1px var(--text-color-grey);
            padding: 0 10px;

            &:first-child {
                border: none;
            }

            & a {
                color: var(--text-color-grey);
                text-decoration: none;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}

header {
    background: var(--grey-background);

    & .logo-container {
        & svg {
            height: 30px;
            width: auto;
        }
    }
}

section {
    background: var(--white-background);
    padding: 100px 0;

    & h1,
    & h2,
    & h3,
    & h4 {
        margin-top: 0;
        color: var(--black);
    }

    &.odd {
        background: var(--blue-background);
        color: var(--text-color-white);

        & h1,
        & h2,
        & h3,
        & h4 {
            color: var(--text-color-white);
        }

        & a {
            color: var(--text-color-white);
        }
    }

    &.grey {
        background: var(--grey-background);
    }

    &.grey2 {
        background: var(--grey2-background);
    }
}

p {
    font-size: 22px;
    font-weight: 600;
}

h1 {
    font-size: 64px;

    & p {
        font-size: 64px;
        font-weight: 700;
        margin: 0;
        padding: 0;
    }
}

h2 {
    font-size: 25px;
    font-weight: 600;
}

h1 + h2 {
    margin-top: -40px;
    margin-bottom: 60px;
}

a {
    color: var(--text-color-black);
    font-size: 18px;
}

.buttons {
    align-items: center;
    display: flex;
    justify-content: center;

    & .button {
        margin: 10px;

        &:first-child {
            margin-left: 0;
        }

        &:last-child {
            margin-right: 0;
        }
    }
}

.button {
    background: var(--white-background);
    border: 1px solid var(--border-dark);
    color: var(--black);
    font-size: 30px;
    font-weight: 600;
    min-width: 280px;
    padding: 25px 35px;
    text-align: center;
    text-decoration: none;
    width: auto;

    &.light {
        border: 1px solid var(--border-grey);
    }
}

article {
    width: 50%;
    padding: 0 60px 0 150px;
    position: relative;

    &:only-child {
        max-width: 1024px;
        padding: 0 60px;
        text-align: center;
        width: 100%;

        & h1 {
            text-align: center;
        }
    }

    & img {
        height: auto;
        width: 100%;
    }
}

aside {
    width: 50%;

    & img {
        height: auto;
        width: 100%;
    }

    & + article {
        padding: 0 150px 0 60px;
    }
}

.video {
    position: relative;
    z-index: 1;
}

.image.screen {
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
    margin-left: -40px;
    margin-top: -40px;
    overflow: hidden;
    position: absolute;
    width: 100%;
    z-index: 0;

    & img {
        margin-top: -2px;
    }
}

#prime {
    & #prime-image {
        position: relative;
    }

    & #prime-text {
        padding-left: 150px;
    }

    & #prime-image-computer {
        position: relative;
        z-index: 1;
    }

    & #prime-image-screen {
        margin-top: 6%;
        position: absolute;
        width: 74%;
        z-index: 2;
    }

    & #prime-image-logo {
        height: 90%;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        z-index: 0;

        & svg {
            height: 100%;
            position: absolute;
            right: -60px;
            width: auto;
        }
    }

    & .buttons {
        justify-content: flex-start;
    }
}

#steps {
    align-items: center;
    display: flex;
    justify-content: space-around;
    margin-bottom: 75px;

    & .step {
        border: 1px solid var(--border-dark);
        border-radius: 50%;
        height: 121px;
        width: 121px;
    }
}

#features {
    & #features-list {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-bottom: 30px;

        & .feature {
            flex-basis: 33.33%;
            flex-grow: 0;
            flex-shrink: 0;
            font-size: 20px;
            margin: 33px 0;
            text-align: center;

            & b {
                font-weight: 600;
            }

            & p {
                font-size: 16px;
                font-weight: 200;
                margin-top: 10px;
            }

            & svg {
                display: block;
                height: 45px;
                margin-bottom: 35px;
                margin-left: 50%;
                transform: translateX(-50%);
                width: 45px;
            }
        }
    }
}

#picture-in-picture {
    & #picture-text {
        margin-bottom: -100px;
        padding: 0 40px;

        & h1 {
            margin-bottom: 80px;
        }

        & #picture-image-capture {
            margin-top: -4px;
        }
    }
}

#unique-features {
    & #unique-text p {
        max-width: 620px;
        margin-bottom: 50px;
        margin-left: auto;
        margin-right: auto;
    }

    & .image img {
        height: auto;
        margin-top: 50px;
        margin-bottom: -100px;
        width: 100%;
    }
}

#buy-it {
    & .buttons {
        margin-top: 60px;
    }
}



